import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '../components/HelloWorld'
import Job from '../pages/job'
import Course from '../pages/course'
import Home from '../pages/home'
import Teacher from '../pages/teacher'
import Enveriment from '../pages/enveriment'
import About from '../pages/about'
import FromSub from '../pages/From'
import News from '../pages/news'


Vue.use(Router)

export default new Router({
  routes: [
   
    {
    	name: 'News',
      path: '/news',//分配的路由地址
      component: News
    },
    {
      path: '/from',//分配的路由地址
      component: FromSub
    },
    {
      path: '/home',//分配的路由地址
      component: Home
    },
     {
      path: '/',//分配的路由地址
      component: Home
    },
    {
      path: '/About',//分配的路由地址
      component: About
    },
    {
      path: '/Course',//分配的路由地址
      component: Course
    },
    {
      path: '/Job',//分配的路由地址
      component: Job
    },
    {
      path: '/enveriment',//分配的路由地址
      component: Enveriment
    },
    {
      path: '/Teacher',//分配的路由地址
      component: Teacher
    }
  ]
  
})



