<template>
	
	<div class="footer">
		<div class="footer-s">
			<router-link :to="{path:'/from'}"><img src="../assets/images_home/tab_iocn_62.png"/></router-link>
			<img @click="tel" src="../assets/images_home/tab_iocn_64.png"/>
			<img src="../assets/images_home/tab_iocn_66.png"/>
		</div>
		<div class="footer-end">
			<span>试听申请</span>
			<span>电话咨询</span>
			<span>在线咨询</span>
		</div>
	</div>
	
	
</template>

<script>
	import { MessageBox } from 'mint-ui';
  export default{
    data(){
      return{
        num:'88888888'
      }
    },
    methods:{
      tel(){
        MessageBox({
          title: '',
          message:'<div style="color:red">'+this.num+'</div>',
          confirmButtonText:'拨打电话'
        }).then(action => {
          window.location.href='tel://'+this.num
      });
      }
    }
  }
</script>

<style>
	.footer{
		position:absolute;
		bottom: 0px;
		height: 49px;
		width: 100%;
		background: #68C04A;
	}
	.footer-end{
		display: block;
		display: flex;
		border-top: 1px solid #68C04A;
		justify-content:space-around;
		
	}
	.footer-end span{
		font-size: 12px;
		color: #fff;
	}
	.footer-s{
		margin-top: 4px;
		display: flex;
		justify-content:space-around;
	}
	.footer img{	
		width: 25px;
		height: 25px;
	}
</style>


