<template>
	
  <div id="app">
  <Topheard></Topheard>
  <transition name="animate"><router-view class="content"></router-view></transition>
  <Footer></Footer>
    
    
  </div>
</template>

<script>
import Topheard from './components/heard'
import Footer from './components/Footer'
import '../static/index.css'

export default {
  components:{Topheard,Footer}
}
</script>

<style>
	
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
.content{
	position: fixed;
	overflow-y: auto;
	top: 90px;
	bottom: 49px;
	width: 100%;
	transition: 0.3s;
}
  .animate-enter{transform:translate(100%,0);opacity: 0}
  .animate-leave-active{transform:translate(-100%,0);opacity: 0}

</style>



