<template>
  <div>
  <h3 class="news-title">{{$route.params.news[$route.params.index].title}}</h3>
    <span class="news-time">发布时间：{{$route.params.news[$route.params.index].time}}</span>
    <p class="news-content" v-html="aaa"></p>

<div @click="back">返回</div>
  </div>
</template>
<script>
  export default {
    data(){
      return{
        news2:this.$route.params.news,
        list:this.$route.params.index,
        aaa:this.$route.params.news[this.$route.params.index].text
      }
    },
  methods:{
    back(){
      this.$router.go(-1)
    }
  }
  }
</script>
